body {
  background-color: #999;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: x-large;
}

h3 {
  width: 100%;
  text-align: center;
  min-height: 34px;
}

div { 
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  list-style: none;
}

div span {  
  margin: 2px;
  background-color: #aaa;
  width: 100%;
  text-align: center;
  flex: 1 1 auto;
}